.CodeOverlay {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.5);
    align-items: center;
    justify-content: center;
}

.Modal { /* modal just for content, overlay includes overlay background */
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
    max-width: 80%; /* Set 3/4 of the page width */
    height: 90%;
    overflow: auto;
    position: relative;
}

.CloseButton {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px;
    background: none;
    border: none;
    cursor: pointer;
}

.Tabs {
    margin-bottom: 10px;
}

.TabButton {
    background-color: #f2f2f2;
    border: none;
    padding: 10px 20px;
    margin-left: 2px;
    margin-right: 1px;
    cursor: pointer;
    border-radius: 5px 5px 0 0;
}

.TabButton:focus {
    outline: none;  
}

.TabContent {
    display: flex;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 0 0 5px 5px;
    overflow-y: auto;/* Enable scrolling within the tab content */
    overflow-x: hidden; /* We don't need horizontal scrolling */
    height: 90%; /* Set a maximum height for the tab content */
}
  