.Button {
    display: flex;
    /* flex: 1; */
    padding: 0px 15px;
    height: 100%;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    /* background-color: hsl(168, 67%, 42%); */
    color: white;
    font-size: 18px;
    font-weight: bold;
    transition: background-color 0.3s, transform 0.2s, box-shadow 0.3s;
}
  
.Button:hover {
    transform: scale(1.05);
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.3);
}