.OthelloBoard {
    display: grid;
    grid-template-columns: repeat(8, minmax(0, 1fr));
    /* grid-template-rows: repeat(8, minmax(0, 1fr)); */
    grid-gap: 2px;
    background-color: white;
}

.GridSquare {
    position: relative;
    align-items: center;
    justify-content: center;
    background-color: green;
    padding-bottom: 100%; /* Maintain square aspect ratio */
}

.BlackDisc {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    height: 80%;
    border-radius: 50%;
    background-color: black;
}

.WhiteDisc {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    height: 80%;
    border-radius: 50%;
    background-color: white;
}

.Highlighted {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #49b9df;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgb(248, 221, 69);
    font-size: calc(min(60vw, calc(100vh - 60px)) / 16);
}

.GameEndText {
    background-color: white;
    color: black;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    height: 15%;
    text-align: center;
    justify-content: center;
    align-items: center;
    display: flex;
    position: absolute;
    z-index: 4;
    border-radius: 20px;
    opacity: 0.95;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
    font-size: 27px;
}

.Loading {
    background-color: white;
    color: black;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 5px 10px;
    text-align: center;
    justify-content: center;
    align-items: center;
    display: flex;
    position: absolute;
    z-index: 3;
    border-radius: 20px;
    opacity: 0.85;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
    font-size: 24px;
}