.TopContainer {
    /* contains scores, undo and reset buttons */
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    margin-top: 10px;
}

.ButtonContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.UndoButton {
    padding: 8px 25px; /* 22 so that undo and reset buttons are same width */
    background-color: #0f4983;
    color: white;
    cursor: pointer;
    border-radius: 1px;
    border: 1px solid #272727;
    margin-bottom: 5px;
    font-size: 26px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
    transition: background-color 0.3s, transform 0.2s, box-shadow 0.3s;
}

.UndoButton:hover {
    background-color: #145ba1;
    transform: scale(1.05);
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.3);
}

.ResetButton {
    padding: 8px 22px;
    background-color: #0f4983;
    color: white;
    cursor: pointer;
    border-radius: 1px;
    border: 1px solid #272727;;
    font-size: 26px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
    transition: background-color 0.3s, transform 0.2s, box-shadow 0.3s;
}

.ResetButton:hover {
    background-color: #145ba1;
    transform: scale(1.05);
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.3);
}

.ScoreContainer {
    /* background-color: white; */
    color: black;
    padding: 0px 30px;
    border-radius: 5px;
    /* border: 1px solid #c0c0c0; */
    display: flex;
    align-items: center;
    justify-content: left;
    font-family: Verdana, sans-serif;
    font-size: 26px;
    margin-left: 5px;
}

.CurrentPlayer {
    transform: scale(1.2);
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.3);
    font-size: 30px;
}
  
.BlackScoreDisc {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: black;
    border: 3px solid black; /* So that is same size as white disc*/
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;  
    font-family: Verdana, sans-serif;
    margin-left: 20px;
    margin-right: 20px;
    font-size: 26px;
    transition: background-color 0.3s, transform 0.2s, box-shadow 0.3s;
}

.WhiteScoreDisc {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: white;
    color: black;
    border: 2px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: Verdana, sans-serif;
    margin-left: 20px;
    font-size: 26px;
    transition: background-color 0.3s, transform 0.2s, box-shadow 0.3s;
}

.MiddleContainer {
    display: flex;
    flex-direction:column;
    align-items: center;
    box-sizing: border-box;
    width: 100%;
}

.PlayerInfoContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px 0px;
    border-radius: 3px;
}

.BlackPlayerInfo {
    padding: 5px 10px;
    margin-right: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 26px;
}

.WhitePlayerInfo {
    padding: 5px 10px;
    margin-left: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 26px;
}

.PlayMoveButton {
    cursor: pointer;
    margin-top: 7px;
    padding: 7px 15px;
    background-color: rgb(0, 129, 43);
    border: 1px solid black;
    border-radius: 3px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
    color: white;
    font-size: 24px;
    transition: transform 0.5s, background-color 0.2s;
}

.PlayMoveButton:hover {
    transform: scale(1.05);
    background-color: rgb(0, 165, 55);
}

.TextWithLines {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    font-size: 26px;
}
  
.HorizontalLine {
    border-top: 1px solid rgb(112, 112, 112);
    width: 70%;
}

.LowerContainer {
    display: flex;
    flex-direction:column;
    align-items: center;
    box-sizing: border-box;
    width: 100%;
}

.AnalysisTable {
    margin-top: 2px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    margin-bottom: 10px;
}

.BlackComputerStats {
    display: flex;
    border: 1px solid black;
    background-color: #000000;
    table-layout: auto;
    color: white;
}

.BlackComputerStats td {
    border: 1px solid white;
    padding: 4px 8px;
    text-align: center;
}

.WhiteComputerStats {
    display: flex;
    border: 1px solid black;
    background-color: white;
    table-layout: auto;
    color: black;
}

.WhiteComputerStats td {
    border: 1px solid black;
    padding: 4px 8px;
    text-align: center;
}