/* App container */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  height: 100vh;
  background-color: white;
}

/* Top banner */
.Banner {
  display: flex;
  justify-content: left;
  align-items: center;
  width: 100%;
  height: 55px;
  box-sizing: border-box;
  background-color: #34495e;
  background-color: rgb(0, 22, 41);
}
/* Main content */
.Main {
  display: flex;
  justify-content: center;
  align-items: top;
  margin-top: 1px;
  width: 100%;
  flex-grow: 1;
  box-sizing: border-box;
}

.OthelloBoardContainer {
  /* Make square shape. 100vh - 60px is height of page without banner */
  width: min(60vw, calc(100vh - 60px));
  height: min(60vw, calc(100vh - 60px));
  margin-left: 1px;
  position: relative;
  box-sizing: border-box;
}

.RightContainer {
  flex-grow: 1;
  height: calc(100vh - 60px);
  margin-left: 10px;
  padding-left: 2px;
  padding-bottom: 2px;
  box-sizing: border-box;
  border: 1px solid #c0c0c0;
  background-color: rgb(248, 248, 248);
  border-radius: 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  justify-content: space-around;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
}

.OpeningOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.OverlayContent {
  text-align: center;
  color: rgb(36, 36, 36);
  padding: 30px;
  width: 50%;
  border: 2px solid rgb(98, 98, 98);
  background-color: white;
  border-radius: 5px;
}

.OverlayHeader {
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 10px;
}

.OverlaySubHeader {
  font-size: 24px;
  margin-bottom: 20px;
}

.OverlayText {
  font-size: 18px;
  color: #666;
}

.OverlayButton {
  padding: 10px 30px; /* 22 so that undo and reset buttons are same width */
  background-color: #23b497;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  margin-bottom: 5px;
  font-size: 18px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
  transition: background-color 0.3s, transform 0.2s, box-shadow 0.3s;
}

.OverlayButton:hover {
    transform: scale(1.05);
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.3);
}
