.Dropdown {
    position: relative;
}

.DropdownToggle {
    cursor: pointer;
    padding-left: 25px;
    padding-right: 25px;
    height: 55px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    color: white;
    font-size: 16px;
    font-weight: bold;
    background-color: #34495e;
    background-color: rgb(0, 22, 41);
    justify-content: space-between;
    transition: color 0.2s;
}
  
.DropdownToggle:hover {
    color: #23b497;
}

.DropdownContent {
    position: absolute;
    top: 100%;
    left: 50%; /* Center horizontally */
    transform: translateX(-50%); /* Adjust for centering */
    background-color: #ecebe8;
    border: 1px solid #ccc;
    text-align: justify; /* Aligns both left and right */
    width: 400px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 20px;
}

.Dropdown:hover .DropdownToggle {
    color: #23b497; /* Show on parent hover */
}

ul li { padding: 5px 20px; }

ol li { padding: 5px 20px; }

.Layer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    box-sizing: border-box;
    padding: 15px 10px;
    width: 100%;
    transition: background-colour 0.3s, transform 0.2s;
}

.Layer:hover {
    transform: scale(1.05);
    background-color: #e2ab33;
}

.EvaluationSelection {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    box-sizing: border-box;
    padding: 15px 10px;
    width: 100%;
}

.NetworkButton {
    cursor: pointer;
    padding: 6px 10px;
    margin-top: 15px;
    margin-left: 10px;
    margin-right: 10px;
    border-radius: 3px;
    font-size: 16px;
    border: 1px solid black;
    background-color: rgb(41, 43, 48);
    color: white;
}

.HeuristicButton {
    background-color: rgb(41, 43, 48);
    color: white;
    cursor: pointer;
    padding: 5px 10px;
    margin-top: 15px;
    font-size: 16px;
}